@charset "UTF-8";

.dimmed_layer_wrapper .full_layer {
    overflow:hidden;
    position:fixed;
    top:0;
    right:0;
    bottom:0;
    left:0;
    width:100%;
    height:100%;
    z-index:100;
    text-align:center;
    white-space:nowrap;
    font-size:0;
    display:inline-block;
    vertical-align:middle;
    height:100%;
    content:'';
}
.common_alert{
    z-index:2;
    position:relative;
    display:inline-block;
    vertical-align:left;
    min-height: 500px;
    max-height:100%;
    min-width: 300px;
    background-color:yellowgreen;
    white-space:normal;
    word-break:break-word;
    text-align:left;
    border-radius:10px;
    padding:30px;
    margin:auto;
    box-sizing:border-box;
    font-size: 16px;
}

.common_alert .header { 
	position:relative;
	font-size: 14px;
	background-color: #000;
	color: white;
	text-align: center;
}

.common_alert .chat { height: 300px; }
.common_alert .chat ul { width: 86%; list-style: none; background-color:white }
.common_alert .chat ul li { width: 100%; color:black}
.common_alert .chat ul li.left { text-align: left; }
.common_alert .chat ul li.right { text-align: right; }

.common_alert .chat ul li > div { font-size: 9px;  }
.common_alert .chat ul li > div.sender { margin: 10px 20px 0 20px; font-weight: bold; }
.common_alert .chat ul li > div.message { display: inline-block; word-break:break-all; margin: 5px 20px; max-width: 65%; border: 1px solid #888; padding: 10px; border-radius: 5px; background-color: #FCFCFC; color: #555; text-align: left; }
 
.common_alert .input-div { position: relative; bottom: 0; width: 100%; text-align: center; border-top: 1px solid #F18C7E; }
.common_alert .input-div > textarea { width: 100%; height: 80px; border: none; padding: 10px; }
 
.format { display: none; }